.market-page {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search-bar-container {
    display: flex;
    flex-direction: row;
    height: 60px;
    width: 70%;
}

.search-bar-container form {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.search-bar-container label {
    width: 20%;
    min-width: 150px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: rgb(170, 222, 212);
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}

.search-bar-container #header-search {
    width: 70%;
    height: 97%;
    padding-left: 20px;
    font-size: 20px;
    border: none;
}

.search-bar-container #submit-button {
    width: 10%;
    height: 100%;
    border: none;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    font-size: 20px;
    background-color: rgb(170, 222, 212);
}

.search-bar-container #submit-button:hover{
    cursor: pointer;
    background-color: rgb(201, 255, 244);
    transition-duration: 0.1s;
}

.content {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100vw;
    margin-top: 2vh;
    column-gap: 5vw;
}

.categories{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 20vw;
    row-gap: 3vh;
}

.listing {
    overflow-y: scroll;
    overflow-x: hidden;
    width: 60vw;
    height: 75vh;
}
